import * as yup from 'yup';

export const phoneRegExp = /(84|0[3|5|7|8|9])+([0-9]{8})\b/;

export const loginSchema = yup.object({
  email: yup.string().required('Vui lng nhp li a ch email').email('Email khng hp l'),
  password: yup.string().required('Vui lng nhp mt khu'),
});

export const registerSchema = yup.object({
  fullName: yup.string().required('Vui lng nhp h tn'),
  phone: yup.string().required('Vui lng nhp s in thoi').matches(phoneRegExp, 'S in thoi khng hp l'),
  email: yup.string().required('Vui lng nhp li a ch email').email('Email khng hp l'),
  password: yup.string().required('Vui lng nhp mt khu').min(6, 'Mt khu phi cha t nht 6 k t'),
});

export const shippingAddressSchema = yup.object({
  phone: yup.string().required('Vui lng nhp s in thoi').matches(phoneRegExp, 'S in thoi khng hp l'),
  address: yup.string().required('Vui lng nhp a ch'),
  name: yup.string().required('Vui lng nhp tn'),
  cityId: yup.number().required('Vui lng chn tnh / thnh ph'),
  districtId: yup.number().required('Vui lng chn qun / huyn'),
  wardId: yup.number().required('Vui lng chn phng / x'),
});

export const updateProfileSchema = yup.object({
  fullName: yup.string().required('Vui lng nhp h tn'),
  phone: yup.string().required('Vui lng nhp s in thoi').matches(phoneRegExp, 'S in thoi khng hp l'),
});

export const changePasswordSchema = yup.object({
  oldPassword: yup.string().required('Vui lng nhp mt khu'),
  password: yup.string().required('Vui lng nhp mt khu mi'),
  confirmPassword: yup.string().required('Vui lng nhp xc nhn mt khu mi').oneOf([yup.ref('password')], 'Mt khu xc nhn khng trng khp'),
});

export const orderSchema = yup.object({
  cityId: yup.number().required('Vui lng chn tnh / thnh ph'),
  districtId: yup.number().required('Vui lng chn qun / huyn'),
  wardId: yup.number().required('Vui lng chn phng / x'),
  address: yup.string().required('Vui lng nhp a ch'),
  name: yup.string().required('Vui lng nhp h v tn'),
  phone: yup.string().required('Vui lng nhp s in thoi').matches(phoneRegExp, 'S in thoi khng hp l'),
  email: yup.string().required('Vui lng nhp li a ch email').email('Email khng hp l'),
});
